/* eslint-disable react/prop-types, react/destructuring-assignment, react/jsx-props-no-spreading */
import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../Layout/Layout';

const Episode = (props) => (
  <Layout>
    <pre>{JSON.stringify(props.data, null, 2)}</pre>
  </Layout>
);

Episode.displayName = 'MKEpisode';

export default Episode;

export const query = graphql`
query allEpisodeMdx ($seasonNr: Int = 0, $episodeNr: Int = 0) {
  episodeMdx: allMdx(filter: {fields: {seasonNr: {eq: $seasonNr}, episodeNr: {eq: $episodeNr}}}) {
    nodes {
      parent {
        ... on File {
          name
        }
      }
      frontmatter {
        title
        choices {
          target
          title
        }
      }
      body
    }
  }
}
`;
